// Spenden.scss

.spenden-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  
    h1 {
      font-size: 24px;
      margin-bottom: 10px;
    }
  
    p {
      margin-bottom: 20px;
    }
  
    .radio-container {
      display: flex;
      gap: 10px;
  
      label {
        display: flex;
        align-items: center;
      }
  
      input[type="radio"] {
        margin-right: 5px;
      }
    }
  
    .input-list-container {
      margin-bottom: 20px;
  
      .input-item {
        margin-bottom: 10px;
  
        input[type="text"] {
          width: 100%;
          padding: 8px;
          border: 1px solid #ccc;
          border-radius: 4px;
        }
      }
    }
  
    button {
      background-color: #4caf50;
      color: white;
      padding: 10px 15px;
      border: none;
      border-radius: 4px;
      cursor: pointer;
  
      &:disabled {
        background-color: #ccc;
        cursor: not-allowed;
      }
    }
  
    .checkbox-container {
      margin-top: 10px;
  
      label {
        display: flex;
        align-items: center;
  
        input[type="checkbox"] {
          margin-right: 5px;
        }
      }
    }
  }
  